import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);
import classNames from 'classnames';

import Comment from './Comment';

export default class Comments extends ParseComponent {
  constructor(props) {
    super(props);

    let story, page;
    if (this.isStory(props)) {
      story = new Parse.Object('Story');
      story.id = props.objectId;
    } else {
      page = new Parse.Object('Page');
      page.id = props.objectId;
    }

    this.state = {
      story: story,
      page: page,
      submitting: false
    };

    this.data = {
      comments: this.props.comments
    };
  }

  observe(props) {
    let commentQuery = new Parse.Query('Comment').include('creator').ascending('createdAt');
    if (this.isStory(props)) {
      commentQuery = commentQuery.equalTo('story', this.state.story);
    } else {
      commentQuery = commentQuery.equalTo('page', this.state.page);
    }

    return {
      comments: commentQuery
    };
  }

  isStory(props) {
    return props.className === 'Story' ? true : false;
  }

  componentDidUpdate() {
    let node = React.findDOMNode(this.refs['message-area']);
    node.scrollTop = node.scrollHeight;
  }

  handleAddComment(e) {
    e.preventDefault();

    let messageValue = React.findDOMNode(this.refs.message).value;

    if (this.state.submitting || messageValue === '') { return; }
    this.setState({submitting: true});

    var msg = messageValue.trim();
    if (!msg) return;

    ParseReact.Mutation.Create('Comment', {
      content: msg,
      story: this.state.story,
      page: this.state.page,
      creator: Parse.User.current()
    }).dispatch().then(() => {
      React.findDOMNode(this.refs.message).value = '';
      this.refreshQueries();
      this.props.incrementCommentCount();
      this.setState({submitting: false});
    });
  }

  render() {
    return (
      <div className='comment-box'>
        <p className='header'>Discussion</p>

        <div className='message-area' ref='message-area'>
          {this.data.comments.map(p => {
            return <Comment {...p} key={p.id.objectId} />;
          })}
        </div>

        <form onSubmit={this.handleAddComment.bind(this)}>
          <input type='text'
                 className={classNames({disabled: this.state.submitting})}
                 ref='message'
                 placeholder='Say something...' />
          <div className='button-spinner-wrapper'>
            <div className={classNames('button-spinner', {hide: !this.state.submitting})}></div>
          </div>
        </form>
      </div>
    );
  }
}

Comments.propTypes = {
  objectId: React.PropTypes.string.isRequired
};

Comments.defaultProps = {
  comments: [],
  objectId: ''
};



