import React from 'react';
import { Parse } from 'parse';
import classNames from 'classnames';

export default class LikeButton extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      objectId: props.objectId,
      isLiked: props.isLiked,
      likeCount: props.likeCount,
      liking: false
    };

    this.toggleLike = this.toggleLike.bind(this);
  }

  toggleLike() {

    if (this.state.liking) {
      return;
    };

    let action = this.state.isLiked ? 'unlikePage' : 'likePage';
    let changeCount = this.state.isLiked ? -1 : 1;
    let storyOrPage = this.props.className === 'Story' ? 'story' : 'page';

    this.setState({liking: true});

    Parse.Cloud.run(action, {id: this.state.objectId, storyOrPage: storyOrPage}, {
      success: (result) => {
        this.setState({
          isLiked: !this.state.isLiked,
          likeCount: this.state.likeCount + changeCount,
          liking: false
        });
      },
      error: (e) => {
        this.setState({liking: false});
      }
    });
  }

  render() {
    let likeClass = classNames({
      'button': true,
      'like-button': true,
      'highlighted': this.state.isLiked
    });

    return (
      <div className={likeClass} onClick={this.toggleLike}>
        <i className="fa fa-heart"></i>
        <span className="like-count">{this.state.likeCount}</span>
      </div>
    );
  }
}

LikeButton.propTypes = {
  isLiked: React.PropTypes.bool,
  likeCount: React.PropTypes.number
};

LikeButton.defaultProps = {
  isLiked: false,
  likeCount: 0
};



