import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);
import classNames from 'classnames';

import ParseAuth from '../../support/ParseAuth';
import MenuItem from './MenuItem';
import UserProfile from '../../support/UserProfile';

export default class Dropdown extends ParseComponent {

  constructor(props) {
    super(props);
    this.state = {
      isShowing: false,
      user: props.user
    };

    this.toggleShowHide = this.toggleShowHide.bind(this);
  }

  observe(props) {
    return {
      user: ParseReact.currentUser
    };
  }

  toggleShowHide() {
    this.setState({isShowing: !this.state.isShowing});
  }

  render() {
    let loginStatus;

    const user = this.data.user;

    if (ParseAuth.loggedIn()) {
      loginStatus = (
        <li>
          <h1 className='signed-in'>Signed in as</h1>
          <h1 className='username'>{UserProfile.getDisplayName(user)}</h1>
        </li>
      );
    } else {
      loginStatus = (
        <li>
          <h1 className='signed-out'>Guest</h1>
        </li>
      );
    }

    return (
      <div>
        <div className='drop-down-menu' onClick={this.toggleShowHide}>
          <img className='avatar' src={UserProfile.getAvatar(user)} />
          <ul className={classNames('sub-menu', 'text-left', {hide: !this.state.isShowing})}>
            {loginStatus}
            {this.props.children}
          </ul>
        </div>
      </div>
    );
  }

}



