import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);

import BookmarkButton from './BookmarkButton';

export default class StoryHeader extends ParseComponent {
  constructor(props) {
    super(props);

    this.state = {
      storyId: props.objectId,
      title: props.title,
      image: props.image,
      bookmarkCount: props.bookmarkCount,
      isBookmarked: false,
      totalLikeCount: props.totalLikeCount,
      pageCount: props.pageCount
    };
  }

  observe(props) {
    return {
      bookmarks: new Parse.Query('Bookmark').equalTo('story', props).equalTo('creator', Parse.User.current()).include('story'),
    };
  }

  imageUrl() {
    if (this.state.image) {
      return this.state.image._url;
    }
  }

  render() {
    let bookmarkButton;

    if (!this.pendingQueries().length) {
      let isBookmarked = this.data.bookmarks.length > 0 && this.data.bookmarks[0].typeRaw == 0;
      bookmarkButton = <BookmarkButton {...this.state} isBig isBookmarked={isBookmarked} />;
    }

    return (
      <div className='grid-container'>
        <div className="vertical grid-block">
          <div className="story-header">
            <img src={this.imageUrl()} />
            <div className="gradient"></div>
            <h1>{this.state.title}</h1>
          </div>
          <div className="story-meta grid-block hidden">
            <div className="bookmark-count grid-block align-center">
              {bookmarkButton}
            </div>
            <div className="total-like-count grid-block align-center">
              <img src={require('../../assets/images/LikeIcon.png')}/>
              {this.state.totalLikeCount}
            </div>
            <div className="page-count grid-block align-center">
              <img src={require('../../assets/images/PageIcon.png')}/>
              {this.state.pageCount}
            </div>
          </div>
        </div>
      </div>
    );
  }
}



