import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);
import classNames from 'classnames';
import objectAssign from 'object-assign';
import SlickSlider from './SlickSlider';

import ApprovePage from './ApprovePage';

export default class ApproveSlider extends ParseComponent {
  constructor(props) {
    super(props);
    this.state = {
      isFiltering: props.isFiltering,
      savedPages: props.savedPages
    };
    this.data = {
      pages: props.pages,
      likes: props.likes
    };
  }

  observe(props) {
    let story = new Parse.Object('Story');
    story.id = props.storyId;
    let refPageId = props.refPageId;

    return {
      pages: new Parse.Query('Page').equalTo('story', story).equalTo('refPageId', refPageId).equalTo('statusRaw', 0).ascending('createdAt').include('creator'),
      user: ParseReact.currentUser
    };
  }

  refreshStory() {
    this.props.refresh();
  }

  toggleFilter() {
    this.setState({
      isFiltering: !this.state.isFiltering
    });
  }

  renderSlider() {
    var pages = this.data.pages || [];

    if (this.state.isFiltering) {
      pages = pages.filter(p => this.state.savedPages[p.id.objectId]);
    }

    if (!pages || !pages.length) {
      return null;
    }

    var settings = {
      className: 'approve-slide-slick',
      // slidesToShow: pages.length > 3 ? 3 : pages.length,
    };

    return (
      <div className='approve-slide-slick-wrapper small-up-3 grid-block'>
        <SlickSlider settings={settings} key={this.state.isFiltering ? 'filterOn' : 'filterOff' }>
          {pages.map((p,i) => {
            const likedPages = this.data.likes &&
              this.data.likes.filter((like) => {
                return like.page !== undefined
                        && like.page.id.objectId === p.id.objectId;
              });

            return (
              <div key={p.id.objectId}>
                <ApprovePage
                  key={p.id.objectId}
                  isLiked={likedPages.length > 0}
                  onSaveClick={this.handlePageSave.bind(this, p)}
                  refreshStory={this.refreshStory.bind(this)}
                  isSaved={this.state.savedPages[p.id.objectId]}
                  {...p} />
              </div>
            );
          }, this)}
        </SlickSlider>
      </div>
    );
  }

  handlePageSave(p, isSaved) {
    this.setState({
      savedPages: objectAssign({}, this.state.savedPages, {
        [p.id.objectId]: isSaved
      })
    });
  }

  render() {
    var filterBtnClass = classNames({
      'fa fa-star star-icon': true,
      'active': this.state.isFiltering
    });

    return (
      <div className='approve-slider-section'>
        <div className='filter-button grid-container' onClick={this.toggleFilter.bind(this)}>
          <i className={filterBtnClass} />
        </div>

        {this.renderSlider()}
      </div>
    );
  }
}

ApproveSlider.propTypes = {
  isFiltering: React.PropTypes.bool.isRequired,
  pages: React.PropTypes.array.isRequired,
  savedPages: React.PropTypes.object.isRequired,
  likes: React.PropTypes.array.isRequired
};

ApproveSlider.defaultProps = {
  isFiltering: false,
  pages: [],
  savedPages: {},
  likes: []
};



