import React from 'react';
import classNames from 'classnames';
import TetherTarget from '../tether/TetherTarget';
import Hint from './Hint';

export default class Highlight extends React.Component {
  constructor(props) {
    super(props);

    this.handleClick = this.handleClick.bind(this);

    this.state = {
      showHint: false,
    };
  }

  handleClick(e) {
    this.setState({showHint: !this.state.showHint});
  }

  render() {
    let result;
    let tetheredOptions = {
      attachment: 'top left',
      targetAttachment: 'bottom right',
      constraints: [
        {to: 'window', attachment: 'together'},
      ],
    };

    let className = this.props.writeAssistOn ? this.props.tag.report : '';

    if (this.state.showHint) {
      result = (
        <TetherTarget
          tethered={
            (<Hint
              category={this.props.tag.categoryDisplayName}
              hint={this.props.tag.hint}
              report={this.props.tag.report}
              onClose={this.handleClick}
            />)}
          tetherOptions={tetheredOptions}
        >
        <span
          className={className}
          onClick={this.handleClick}
        >{this.props.children}</span>
      </TetherTarget>
      );
    } else {
      return (
        <span
          className={className}
          onClick={this.handleClick}
        >
          {this.props.children}
        </span>
      );
    };

    return result;
  }
}



