import _ from 'lodash';
import slug from 'slug';
import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
import NotificationSystem from 'react-notification-system';
import ReactDOM from 'react-dom';
import UserProfile from '../../support/UserProfile';
import {requireAuth} from '../../decorators/requireAuth';
import SelectLabels from './SelectLabels';
import PageTextArea from '../pages/PageTextArea';
import EditableStoryHeader from './EditableStoryHeader';
import LabelTypeConstants from '../../constants/LabelTypeConstants';
import StoryTypeConstants from '../../constants/StoryTypeConstants';
import StoryStatusConstants from '../../constants/StoryStatusConstants';
import classNames from 'classnames';

const ParseComponent = ParseReact.Component(React);

function toOptions (labels) {
  return labels.map((label) => {
    return {
      label: label.name,
      value: slug(label.name),
    };
  });
}

function getOptions(labels) {
  return _(labels).groupBy('typeRaw')
    .mapValues(toOptions)
    .value();
}

const NewStory = class NewStory extends ParseComponent {
  constructor(props) {
    super(props);
    this.validate = this.validate.bind(this);
    this.handleDrop = this.handleDrop.bind(this);
    this.updateStory = this.updateStory.bind(this);
    this.uploadImage = this.uploadImage.bind(this);
    this.handleCreate = this.handleCreate.bind(this);
    this.createNewStory = this.createNewStory.bind(this);
    this.handleSaveDraft = this.handleSaveDraft.bind(this);
    this.handleTitleChange = this.handleTitleChange.bind(this);
    this.handleLabelChange = this.handleLabelChange.bind(this);
    this.handleWriteAssist = this.handleWriteAssist.bind(this);
    this.getSelectedLabels = this.getSelectedLabels.bind(this);
    this.handleContentChange = this.handleContentChange.bind(this);

    this.state = {
      title: props.title,
      files: props.files,
      content: null,
      saveDraft: false,
      classOptions: [],
      genreOptions: [],
      assignmentOptions: [],
      miscOptions: [],
      storyId: null,
      creating: false,
      saving: false,
      assisting: false,
      _notificationSystem: null,
      aids: null,
      initialized: false,
      imageUrl: null,
    };
  }

  observe(props, state) {
    let storyQuery = (new Parse.Query('Story'))
      .equalTo('creator', Parse.User.current())
      .equalTo('statusRaw', StoryStatusConstants.DRAFTED)
      .descending('createdAt');

    let query = {
      labels: new Parse.Query('Label'),
      user: ParseReact.currentUser,
      stories: storyQuery,
    };

    return query;
  }

  componentDidMount() {
    this._notificationSystem = this.refs.notificationSystem;
  }

  componentDidUpdate(prevProps, prevState) {
    if (!this.state.initialized && this.data.stories && this.data.stories.length) {
      const story = this.data.stories[0];
      let change = {};
      if (!this.state.storyId) {
        change.storyId = story.objectId;
      };
      if (!this.state.content) {
        change.content = story.content;
      };
      if (!this.state.title) {
        change.title = story.title;
      };
      if (!this.state.files && story.image) {
        change.imageUrl = story.image._url ? story.image._url : story.image.url;
      };
      if (story.labels.length) {
        const ids = story.labels.map((label) => label.objectId);
        const savedOptions = getOptions(this.data.labels.filter((label) => {
          return ids.indexOf(label.objectId) !== -1;
        }));

        if (!this.state.classOptions.length) {
          change.classOptions = savedOptions[LabelTypeConstants.CLASS] || [];
        };
        if (!this.state.genreOptions.length) {
          change.genreOptions = savedOptions[LabelTypeConstants.GENRE] || [];
        };
        if (!this.state.assignmentOptions.length) {
          change.assignmentOptions = savedOptions[LabelTypeConstants.ASSIGNMENT] || [];
        };
        if (!this.state.miscOptions.length) {
          change.miscOptions = savedOptions[LabelTypeConstants.MISC] || [];
        };
      };

      this.setState({...change, initialized: true});
    };
  }

  uploadImage() {
    let p;
    if (this.state.files) {
      const image = new Parse.File('file', this.state.files[0]);
      p = image.save();
    } else {
      p = Parse.Promise.as();
    };
    return p;
  }

  getSelectedLabels() {
    return [this.state.classOptions,
        this.state.genreOptions,
        this.state.assignmentOptions,
        this.state.miscOptions].map((options, i) => {
      return options.map((option) => {
        return this.data.labels.find((label) => {
          return (slug(label.name) === option.value) && (label.typeRaw === i);
        });
      });
    }).reduce((a, b) => a.concat(b));
  }

  createNewStory(status) {
    return this.uploadImage().then((image) => {
      return ParseReact.Mutation.Create('Story', {
        approver: this.data.user[0],
        creator: this.data.user[0],
        title: this.state.title,
        content: this.state.content,
        statusRaw: status,
        image: image,
        labels: this.getSelectedLabels(),
        typeRaw: StoryTypeConstants.PUBLIC,
      }).dispatch();
    });
  }

  updateStory(options) {
    this.setState({saving: true});
    return this.uploadImage().then((image) => {
      if (image) {
        options.image = image;
      };
      return ParseReact.Mutation.Set(this.data.stories[0], {...options}).dispatch();
    }).then((result) => {
      this.setState({saving: false});
    }, (error) => {
      this.setState({saving: false});
    });
  }

  handleLabelChange(type, value, options) {
    this.setState({
      classOptions: type === LabelTypeConstants.CLASS ? options : this.state.classOptions,
      genreOptions: type === LabelTypeConstants.GENRE ? options : this.state.genreOptions,
      assignmentOptions: type === LabelTypeConstants.ASSIGNMENT ? options : this.state.assignmentOptions,
      miscOptions: type === LabelTypeConstants.MISC ? options : this.state.miscOptions,
    });

    const sluggedOptions = options.map((option) => {
      const slugged = slug(option.value);
      return {...option, slug: slugged};
    });

    const newOptions = sluggedOptions.filter((option) => {
      const slugged = this.data.labels.map(l => slug(l.name));
      return slugged.indexOf(option.slug) === -1;
    });
  }

  handleTitleChange(title) {
    this.setState({title: title});
  }

  handleDrop(files) {
    this.setState({files: files});
  }

  handleContentChange(text) {
    this.setState({content: text});
  }

  handleWriteAssist(e) {
    if (this.state.content && this.state.content.length) {
      this.setState({assisting: true});
      Parse.Cloud.run('getAid', {text: this.state.content}, (response) => {
        this.setState({aids: JSON.parse(response.text), showHint: true, assisting: false});
      });
    };
  }


  handleSaveDraft(e) {
    e.preventDefault();
    if (this.state.content && this.state.content.length > 400) {
      this._notificationSystem.addNotification({
        message: 'Your story content must be less than 400 characters!',
        level: 'error'
      });
    } else if (!this.state.saving) {
      if (!this.state.storyId) {
        this.createNewStory(StoryStatusConstants.DRAFTED).then((result) => {
          this.setState({storyId: result.objectId});
          this.refreshQueries();
          this.setState({saving: false});
        });
        this.setState({saveDraft: true});
      } else {
        this.updateStory({
          content: this.state.content,
          labels: this.getSelectedLabels(),
          title: this.state.title,
        }).then((result) => {
          this.setState({saving: false});
        });
      };
    };
  }

  validate() {
    if (!this.state.title) {
      this._notificationSystem.addNotification({
        message: 'Your story must have a Title!',
        level: 'error'
      });
      return false;
    } else if (!this.state.files && !this.state.imageUrl){
      this._notificationSystem.addNotification({
        message: 'Your story must have a picture!',
        level: 'error'
      });
      return false;
    } else if (this.state.content && this.state.content.length > 400) {
      this._notificationSystem.addNotification({
        message: 'Your story content must be less than 400 characters!',
        level: 'error'
      });
      return false;
    } else {
      return true;
    }
  }

  handleCreate(e) {
    e.preventDefault();
    if (this.validate() && !this.state.creating) {
      console.log('validated');
      this.setState({creating: true});

      if (!this.state.storyId) {
        this.createNewStory(StoryStatusConstants.ONGOING).then((result) => {
          this.setState({creating: false});
          this.context.router.transitionTo('story', {id: result.objectId});
        });
      } else {
        this.updateStory({
          content: this.state.content,
          labels: this.getSelectedLabels(),
          statusRaw: StoryStatusConstants.ONGOING
        }).then((result) => {
          this.setState({creating: false});
          this.context.router.transitionTo('story', {id: this.state.storyId});
        });
      };
    };
  }

  render() {

    const groupedLabels = getOptions(this.data.labels);
    const user = this.data.user;

    return (
      <section className='story vertical'>
        <div className='story-container grid-block'>
          <EditableStoryHeader
            onChange={this.handleTitleChange}
            onDrop={this.handleDrop}
            title={this.state.title}
            imageUrl={this.state.imageUrl}
          />
        </div>
        <div className='new-story-content grid-container'>
          <div className='grid-block labels'>
            <div className='grid-block vertical'>
              <SelectLabels
                name='Class'
                value={this.state.classOptions}
                options={groupedLabels[LabelTypeConstants.CLASS]}
                onChange={this.handleLabelChange.bind(this, LabelTypeConstants.CLASS)}
              />
              <SelectLabels
                name='Genre'
                value={this.state.genreOptions}
                options={groupedLabels[LabelTypeConstants.GENRE]}
                onChange={this.handleLabelChange.bind(this, LabelTypeConstants.GENRE)}
              />
              <SelectLabels
                name='Assignment'
                value={this.state.assignmentOptions}
                options={groupedLabels[LabelTypeConstants.ASSIGNMENT]}
                onChange={this.handleLabelChange.bind(this, LabelTypeConstants.ASSIGNMENT)}
              />
              <SelectLabels
                name='Misc'
                value={this.state.miscOptions}
                options={groupedLabels[LabelTypeConstants.MISC]}
                onChange={this.handleLabelChange.bind(this, LabelTypeConstants.MISC)}
              />
            </div>
          </div>
          <div className='new-submission vertical grid-block'>
            <PageTextArea
              placeholder='Write the first bit (optional)'
              avatarUrl={UserProfile.getAvatar(user)}
              onChange={this.handleContentChange}
              aids={this.state.aids}
              showHint={this.state.showHint}
              value={this.state.content}
            />
            <div className='button-actions grid-block align-right'>
              <div className='small-4 small-offset-1 medium alert hollow button hide'>
                Cancel
                <div className='button-spinner alert'></div>
              </div>
              <div className='small-5 medium hollow info button'
                onClick={this.handleWriteAssist} ref='writeAssistBtn'
              >
                <span className={classNames({hide: this.state.assisting})}>Write Assist</span>
                <div className={classNames('button-spinner', 'info', {hide: !this.state.assisting})}></div>
              </div>

              <div
                className='small-5 small-offset-7 medium hollow button'
                onClick={this.handleSaveDraft}
              >

                <span className={classNames({hide: this.state.saving})}>Save Draft</span>
                <div className={classNames('button-spinner', {hide: !this.state.saving})}></div>
              </div>
              <div
                className='create-button small-4 small-offset-1 medium success hollow button'
                onClick={this.handleCreate}
              >
                <span className={classNames({hide: this.state.creating})}>Create</span>
                <div className={classNames('button-spinner', 'success', {hide: !this.state.creating})}></div>
              </div>
              <div>
                <NotificationSystem ref="notificationSystem" />
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
};

export default requireAuth(NewStory);
export let undecorated = NewStory;

NewStory.contextTypes = {
  router: React.PropTypes.func
};

NewStory.defaultProps = {
  title: null,
  files: null
};



