import React from 'react';
import Select from 'react-select';
import Label from './Label';

export default class SelectLabels extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className='select-labels grid-block'>
        <div className="grid-content shrink v-align">
          {this.props.name + ' : '}
        </div>
        <div className="grid-content">
          <Select
            multi
            clearable={false}
            placeholder=''
            valueComponent={Label}
            {...this.props}/>
        </div>
      </div>
    );
  }
}

SelectLabels.propTypes = {
  allowCreate: React.PropTypes.bool,
  options: React.PropTypes.array,
};

SelectLabels.defaultProps = {
  allowCreate: false
};



