'use strict';

var _ = require('lodash');
var React = require('react');
var Drop = require('tether-drop');


// NOTE: This is not a React component. It's a plain JS object that manages a React component.

function DropElement(dropOptions) {
    this.domNode = document.createElement('div');
    this.domNode.style.position = 'absolute'; // needed for Tether
    document.body.appendChild(this.domNode);

    this.drop = new Drop(_.merge({
        content: this.domNode,
    }, dropOptions));
}

DropElement.prototype.setElement = function (reactComponent) {
    this.reactComponent = reactComponent;
    this.update();
};

DropElement.prototype.update = function () {
    React.render(
        this.reactComponent,
        this.domNode,
        () => this.drop.position()
    );
};

DropElement.prototype.update = function () {
    React.render(
        this.reactComponent,
        this.domNode,
        () => this.drop.position()
    );
};

DropElement.prototype.close = function () {
    this.drop.close();
};

DropElement.prototype.destroy = function () {
    React.unmountComponentAtNode(this.domNode);
    this.domNode.parentNode.removeChild(this.domNode);
    this.drop.destroy();
};

module.exports = DropElement;


