import React from 'react';
import { Parse } from 'parse';
import Dropzone from 'react-dropzone';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);

const propTypes = {
  onDrop: React.PropTypes.func
};

export default class UploadAvatar extends ParseComponent {

  constructor() {
    super();

    this.onDrop = this.onDrop.bind(this);
  }

  onDrop(files) {
    if (this.props.onDrop) {this.props.onDrop(files);};
  }

  observe() {
  }

  render() {

    return(
      <div className='choose-avatar-block grid-block'>
        <div className='grid-container small-18 avatar-section'>
          <h1>Choose avatar</h1>
          <h2 className='avatar-subheading'>Upload</h2>
          <div className='grid-block align-center'>
            <Dropzone onDrop={this.onDrop} className="avatar-dropzone">
              <img className='upload-avatar-icon' src={require('../../assets/images/Upload.png')}/>
            </Dropzone>
          </div>
        </div>
      </div>
    );
  }
}

UploadAvatar.propTypes = propTypes;



