import React from 'react';
import ErrorMessages from '../layouts/ErrorMessages';
import zxcvbn from 'zxcvbn';
import classNames from 'classnames';
import ChooseAvatar from './ChooseAvatar';
import UploadAvatar from './UploadAvatar';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);

import { Parse } from 'parse';

export default class Edit extends ParseComponent {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      passwordStrength: 0,
      enableSubmit: false,
      newPasswordSaved: false
    };
    this.handlePasswordChange = this.handlePasswordChange.bind(this);
    this.handleConfirmPasswordChange = this.handleConfirmPasswordChange.bind(this);
    this.getPassword = this.getPassword.bind(this);
    this.getConfirmPassword = this.getConfirmPassword.bind(this);
    this.clickSubmit = this.clickSubmit.bind(this);
    this.handleAvatarChange = this.handleAvatarChange.bind(this);
    this.handleAvatarUpload = this.handleAvatarUpload.bind(this);
  }

  observe(){
    return {
      user: ParseReact.currentUser
    };
  }

  getPassword() {
    return React.findDOMNode(this.refs.password).value;
  }

  getConfirmPassword() {
    return React.findDOMNode(this.refs.confirmPassword).value;
  }

  handlePasswordChange(e) {
    const strength = zxcvbn(this.getPassword()).score;

    this.setState({
      passwordStrength: strength,
      enableSubmit: strength >= 2 && this.getPassword() === this.getConfirmPassword()
    });
  }

  handleConfirmPasswordChange(e) {
    this.setState({
      enableSubmit: this.state.passwordStrength >= 2 && this.getPassword() === this.getConfirmPassword()
    });
  }

  handleAvatarUpload(file) {
    const image = new Parse.File('file', file[0]);
    image.save().then(() => {
      ParseReact.Mutation.Set(this.data.user, {smallAvatar: image}).dispatch();
    });
  }

  handleAvatarChange(avatar) {
    ParseReact.Mutation.Set(this.data.user, {smallAvatar: avatar.image}).dispatch();
  }

  clickSubmit(e) {
    e.preventDefault();
    if (this.state.enableSubmit) {
      const user = Parse.User.current();
      ParseReact.Mutation.Set(this.data.user, {password: this.getPassword()}).dispatch();
      this.setState({newPasswordSaved: true});
    };
  }

  render() {
    let strength = this.state.passwordStrength,
      disabled = this.state.enableSubmit ? '' : 'disabled',
      errorMessage,
      strengthText,
      strengthClass;

    let newPasswordSaved;
    if (this.state.newPasswordSaved) {
      newPasswordSaved = <p className='password-saved-text'>New password saved</p>;
    }

    if (this.state.error) {
      errorMessage = <ErrorMessages messages={[this.state.error]}/>;
    };

    switch (strength) {
      case 0:
        strengthClass = '';
        strengthText = 'Password too weak';
        break;
      case 1:
        strengthClass = 'low';
        strengthText = 'Password too weak';
        break;
      case 2:
        strengthClass = 'medium';
        strengthText = 'Password fair';
        break;
      case 3:
      case 4:
        strengthClass = 'high';
        strengthText = 'Password strong';
        break;
    }

    return (
      <div>
        <form className='session grid-block' onKeyDown={this.keyDown}>
          <div className='grid-container small-18'>
            <h1>Reset Password</h1>
            <input ref='password'className='password'type='password'placeholder='New password'onChange={this.handlePasswordChange} />
            <input ref='confirmPassword'className='confirm-password'type='password'placeholder='Confirm your password'onChange={this.handleConfirmPasswordChange} />
            <div className='grid-block align-right'>
              <div className='small-16 grid-block'>
                <progress className={strengthClass} value={strength} max='4'/>
              </div>
              <div className='small-6 small-offset-2 align-right grid-block'>
                <p>{strengthText}</p>
              </div>
            </div>
            <div className='grid-block align-right'>
              <div className='grid-block align-left'>
                {newPasswordSaved}
              </div>
              <div className='grid-block align-right'>
                <input className={classNames('button', {disabled})} type='submit'value='Submit'onClick={this.clickSubmit}/>
              </div>
            </div>
            { errorMessage }
          </div>
        </form>
        <UploadAvatar onDrop={this.handleAvatarUpload}/>
        <ChooseAvatar onChange={this.handleAvatarChange}/>
      </div>
    );
  }
}



