import React from 'react';

export default class WriteAssistButton extends React.Component {
  constructor(props) {
    super(props);

    this.handleClickWriteAssist = this.handleClickWriteAssist.bind(this);
  }

  handleClickWriteAssist(e) {
    this.props.onClick(e);
  }

  render() {
    return (
      <div className='write-assist'>
        <a onClick={this.handleClickWriteAssist}>
          <img
            src={this.props.isOn ? require('../../assets/images/WriteAssistOn.png') : require('../../assets/images/WriteAssistOff.png')}
          />
        </a>
        <p>{this.props.isOn ? 'Write Assist On': 'Write Assist Off'}</p>
      </div>
    );
  }
}



