import React from 'react';
import { Parse } from 'parse';
import classNames from 'classnames';
import Isvg from 'react-inlinesvg';

export default class BookmarkButton extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      storyId: props.storyId,
      isBookmarked: props.isBookmarked,
      bookmarkCount: props.bookmarkCount,
      isBig: props.isBig,
      buttonSize: {width: 38, height: 32},
      bookmarking: false
    };

    this.toggleBookmarkStory = this.toggleBookmarkStory.bind(this);
  }

  onLoad() {
    if (!this.state.isBig) return;

    let node = React.findDOMNode(this.refs['bookmark-icon']);
    let svg = node.children[0];
    svg.setAttribute('width', this.state.buttonSize.width);
    svg.setAttribute('height', this.state.buttonSize.height);
  }

  toggleBookmarkStory(e) {

    if (this.state.bookmarking) {
      return;
    };

    let action = this.state.isBookmarked ? 'unbookmarkStory' : 'bookmarkStory';
    let changeCount = this.state.isBookmarked ? -1 : 1;

    this.setState({bookmarking: true});

    Parse.Cloud.run(action, {id: this.state.storyId}, {
      success: (result) => {
        this.setState({
          isBookmarked: !this.state.isBookmarked,
          bookmarkCount: this.state.bookmarkCount + changeCount,
          bookmarking: false
        });
      },
      error: (e) => {
        this.setState({bookmarking: false});
      }
    });
  }

  render() {
    let bookmarkClass = classNames({
      bookmarks: true,
      highlighted: this.state.isBookmarked
    });
    return (
      <div className={bookmarkClass} onClick={this.toggleBookmarkStory}>
        <Isvg src={require('../../assets/images/ic_bookmark_border_black_24px.svg')}
              ref='bookmark-icon'
              onLoad={this.onLoad.bind(this)} />{this.state.bookmarkCount}
      </div>
    );
  }
}

BookmarkButton.propTypes = {
  isBookmarked: React.PropTypes.bool,
  isBig: React.PropTypes.bool
};

BookmarkButton.defaultProps = {
  isBookmarked: false,
  isBig: false
};



