const profile = {
  getDisplayName(user) {
    if (!!user) {
      if (user.displayName) {
        return user.displayName;
      } else if (user.username) {
        return user.username;
      }
    }

    return 'Jane Doe';
  },

  getAvatar(user) {
    let avatar;

    if (!!user) {
      if (user.smallAvatar) {
        avatar = user.smallAvatar;
      } else if (user.largeAvatar) {
        avatar = user.largeAvatar;
      }

      // FIXME: #85
      if (avatar && avatar._url) {
        return avatar._url;
      } else if (avatar && avatar.url) {
        return avatar.url;
      }
    }

    return require('../assets/images/avatar.png');
  }
};

export default profile;


