import React from 'react';
import { RouteHandler } from 'react-router';
import NavigationBar from '../navigation/NavigationBar';

export default class App extends React.Component {
  render() {
    return (
      <div className="app">
        <NavigationBar />
        <RouteHandler />
      </div>
    );
  }
}

App.contextTypes = {
  router: React.PropTypes.func
};



