import React from 'react';
import Editor from '../medium/Editor';

export default class PageTextArea extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      text: this.props.value
    };

    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(text) {
    this.setState({text});

    if (this.props.onChange) {this.props.onChange(text);};
  }

  render() {
    return (
      <div className='textarea-container grid-block small-2-up'>
        <div className='avatar small-3 align-center'>
          <img src={this.props.avatarUrl} />
        </div>
        <Editor
          className='grid-block textarea small-20'
          onChange={this.handleChange}
          showHint={this.props.showHint}
          aids={this.props.aids}
          text={this.props.value}
          options={
            {
              toolbar: false,
              disableReturn: true,
              placeholder: {
                text: this.props.placeholder,
              }
            }
          }
        />
      </div>
    );
  }
}

PageTextArea.propTypes = {
  avatarUrl: React.PropTypes.string,
  placeholder: React.PropTypes.string,
  value: React.PropTypes.string,
  onChange: React.PropTypes.func,
  showHint: React.PropTypes.bool,
};

PageTextArea.defaultProps = {
  avatarUrl: require('../../assets/images/avatar.png'),
  placeholder: 'What happens next?',
  value: null,
  showHint: false,
};


