import React from 'react';
import Dropzone from 'react-dropzone';

import { randomTitleGenerator } from './randomTitleGenerator';

export default class StoryHeader extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      image: null,
      title: '',
      initialized: false,
    };

    this.onDrop = this.onDrop.bind(this);
    this.titleGeneratorClick = this.titleGeneratorClick.bind(this);
    this.titleCreate = this.titleCreate.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.title && !this.state.title) {
      this.setState({title: nextProps.title});
    };
  }

  onDrop(files) {
    this.setState({
      image: files[0]
    });
    this.props.onDrop(files);
  }

  titleGeneratorClick() {
    let newTitle = randomTitleGenerator();
    this.setState({title: newTitle});
    this.props.onChange(newTitle);
  }

  titleCreate(e) {
    this.setState({title: e.target.value});
    this.props.onChange(e.target.value);
  }

  render() {
    const style = {
      position: 'absolute',
      top: 0,
      left: 0,
      bottom: 0,
      right: 0
    };

    const preview = this.state.image && this.state.image.preview || this.props.imageUrl;

    return (
      <div className='grid-container'>
        <div className="vertical grid-block">
          <div className="story-header editable">
            <Dropzone onDrop={this.onDrop} style={style}>
              <img className='upload-icon' src={require('../../assets/images/Upload.png')}/>
              <img className='preview' src={preview}/>
            </Dropzone>
            <div className="gradient"></div>
            <a onClick={this.titleGeneratorClick}>
              <img className='story-slotmachine-icon' src={require('../../assets/images/SlotMachine.png')}/>
            </a>
            <input
              className='story-title'
              name='story-title'
              type='text'
              placeholder='Title'
              value={this.state.title}
              onChange={this.titleCreate}
            />
          </div>
        </div>
      </div>
    );
  }
}



