import React from 'react';
import { spring } from 'react-motion';
import Transition from 'react-motion-ui-pack';

import Page from './Page';
import PageSlider from './PageSlider';
import Comments from '../comments/Comments';

export default class PageRow extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      className: this.props.className,
      viewType: this.props.viewType,
      commentBoxOpen: false
    };

    this.onClickComment = this.onClickComment.bind(this);
    this.incrementCommentCount= this.incrementCommentCount.bind(this);
  }

  handleOnToggleViewType() {
    this.setState({
      viewType: this.state.viewType == 'PAGE' ? 'SLIDER' : 'PAGE',
      commentBoxOpen: this.state.viewType == 'PAGE' ? false : this.state.commentBoxOpen
    });
  }

  onClickComment(pageId) {
    this.props.onShowCommentsBox();
    this.setState({commentBoxOpen: !this.state.commentBoxOpen});
  }

  hideCommentBox() {
    this.setState({commentBoxOpen: false});
    this.refs['page'].deselectComment();
  }

  incrementCommentCount() {
    this.refs['page'].incrementCommentCount();
  }

  renderPage() {
    let showMore;
    if (this.props.className !== 'Story' && this.props.morePageCount > 0) {
      showMore = (
        <div className='toggle-view-button'
             onClick={this.handleOnToggleViewType.bind(this)}>
          {this.props.morePageCount}<br/>More<br/><i className="fa fa-ellipsis-h"></i>
        </div>
      );
    }
    return (
      <div className='page-wrapper grid-container'>
        <div className='center'>
          <Page {...this.props}
                ref='page'
                onClickComment={this.onClickComment} />

          {showMore}

          <Transition
            onlyChild
            appear
            enter={{
              opacity: 1,
            }}
            leave={{
              opacity: 0,
            }}
          >
          {
            this.state.commentBoxOpen &&
            <div className='comment-box-wrapper'>
              <Comments incrementCommentCount={this.incrementCommentCount}
                        className={this.props.className}
                        objectId={this.props.id.objectId} />
            </div>
          }

          </Transition>
        </div>
      </div>
    );
  }

  renderSlider() {
    return (
      <PageSlider {...this.props} onClose={this.handleOnToggleViewType.bind(this)} />
    );
  }

  render() {
    var contentElement = {
      PAGE: this.renderPage(),
      SLIDER: this.renderSlider(),
    }[this.state.viewType];

    return (
      <div className='page-row-outer'>
        {contentElement}
      </div>
    );
  }
}

PageRow.propTypes = {
  viewType: React.PropTypes.oneOf(['PAGE', 'SLIDER']),
  onShowCommentsBox: React.PropTypes.func.isRequired
};

PageRow.defaultProps = {
  viewType: 'PAGE'
};



