import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);

const propTypes = {
  onChange: React.PropTypes.func
};

export default class ChooseAvatar extends ParseComponent {

  constructor() {
    super();

    this.onClickImage = this.onClickImage.bind(this);
  }

  observe() {
    return {
      avatars: (new Parse.Query('Avatar')),
    };
  }

  renderAvatar() {
    return (
      this.data.avatars.map((s, i) => {
        let avatarImgs = s.image._url;

        return (
          <div className='grid-block align-center' key={s.objectId}>
            <a onClick={this.onClickImage.bind(this, i)}>
              <img className='avatar-img' src={avatarImgs} />
            </a>
          </div>
        );
      })
    );
  }

  onClickImage(i, e) {
    e.preventDefault();
    if (this.props.onChange) {this.props.onChange(this.data.avatars[i]);};
  }

  render() {
    let avatarImgs = this.renderAvatar();

    return(
      <div className='choose-avatar-block grid-block'>
        <div className='grid-container small-18 avatar-section'>
          <h2 className="avatar-subheading">Select</h2>
            <div className='vertical medium-horizontal grid-block small-up-4'>
              {avatarImgs}
            </div>
        </div>
      </div>
    );
  }
}

ChooseAvatar.propTypes = propTypes;



