import React from 'react';
import { Parse } from 'parse';
import { Link } from 'react-router';
import classNames from 'classnames';
import Isvg from 'react-inlinesvg';

import BookmarkButton from './BookmarkButton';

export default class StoryListItem extends React.Component {

  constructor(props) {
    super(props);

    this.initData(props);

    this.onClickLink = this.onClickLink.bind(this);
  }

  initData(props) {
    let bookmarkCount = 0, totalLikeCount = 0, pageCount = 0;
    if (this.props.bookmarkCount !== undefined) {
      bookmarkCount = this.props.bookmarkCount;
    }
    if (this.props.totalLikeCount !== undefined) {
      totalLikeCount = this.props.totalLikeCount;
    }
    if (this.props.pageCount !== undefined) {
      pageCount = this.props.pageCount;
    }

    this.state = {
      storyId: this.props.id.objectId,
      image: this.props.image,
      title: this.props.title,
      bookmarkCount: bookmarkCount,
      isBookmarked: this.props.isBookmarked,
      totalLikeCount: totalLikeCount,
      pageCount: pageCount
    };
  }

  onClickLink(e) {
    e.preventDefault();
    this.context.router.transitionTo('story', {id: this.state.storyId});
  }

  formatData() {
    if (this.state.pageCount === undefined) {
      // this.setState({pageCount: 0});
      this.state.pageCount = 0;
    }
  }

  // Helpers
  titleContent() {
    return(<h1>{this.state.title}</h1>);
  }

  imageContent() {
    if (this.state.image) {
      return (
          <img className='story-image' src={this.state.image._url} />
      );
    }
    return (
      <img className='story-image' src={require('../../assets/images/little_red_riding_hood.jpg')} />
    );
  }

  likesContent() {
    return (
      <span className='likes'>
        <Isvg src={require('../../assets/images/ic_favorite_black_24px.svg')} />{this.state.totalLikeCount}
      </span>
    );
  }

  pagesContent() {
    return (
      <span className='pages'>
        <Isvg src={require('../../assets/images/ic_reply_black_24px.svg')} />{this.state.pageCount}
      </span>
    );
  }

  render() {
    return (
      <div className='story-list-item vertical grid-block'>
        <header className='grid-content small-24 square-aspect-ratio'>
          <div className='content' onClick={this.onClickLink}>
            {this.imageContent()}
            <div className='gradient-overlay'/>
            {this.titleContent()}
          </div>
        </header>
        <footer className="grid-block">
          <div className="grid-block small-10">
            <BookmarkButton {...this.state} />
          </div>
          <div className="grid-block small-14 align-right">
            {this.likesContent()}
            {this.pagesContent()}
          </div>
        </footer>
      </div>
    );
  }

}

StoryListItem.contextTypes = {
  router: React.PropTypes.func
};

StoryListItem.propTypes = {
  isBookmarked: React.PropTypes.bool
};

StoryListItem.defaultProps = {
  isBookmarked: false
};



